import { format } from 'date-fns';
const formatSailingTime = time =>
  time ? format(new Date(time).getTime(), 'HH:mm') : time;
export default props => (
  <div>
    <h1>{formatSailingTime(props.scheduledDeparture)}</h1>
    <h2>{props.vessel}</h2>
    <h3>{formatSailingTime(props.actualDeparture)}</h3>
    <h3>{formatSailingTime(props.eta)}</h3>
    <h3>{props.sailingStatus}</h3>
  </div>
);
